body{font-family:'brandontext_regular', sans-serif;font-size:13px;line-height:1.6;background:#f4f8fa}.sc_item{margin:150px 0}.sc_item__left .txt,.sc_item__right .txt{overflow:hidden}.sc_item__left .entry-head,.sc_item__right .entry-head{text-align:center}.sc_item__left .entry-head .title,.sc_item__right .entry-head .title{font-family:"Times New Roman", serif;font-weight:normal;font-size:80px;font-style:italic;color:#707070;line-height:1;margin-bottom:15px}.sc_item__left .entry-head .sub-title,.sc_item__right .entry-head .sub-title{font-size:24px;color:#002040;text-decoration:none;display:block;margin-bottom:60px}.sc_item__left .entry-content,.sc_item__right .entry-content{font-size:20px;color:#707070}.sc_item__left .read-more,.sc_item__right .read-more{font-size:18px;color:#fff;display:inline-block;background:#002040;text-align:center;padding:15px 80px;-webkit-border-radius:3px;-moz-border-radius:3px;border-radius:5px;text-decoration:none;margin:40px 0}.sc_item__left{overflow:auto;margin-bottom:50px}.sc_item__left a img{width:45%;float:left;margin-right:90px}.sc_item__left a img:hover{opacity:0.85}.sc_item__left h3{margin-top:0}.sc_item__left h3:hover{color:#00a651}.sc_item__left p{text-align:justify}.sc_item__right{overflow:auto;margin-bottom:50px}.sc_item__right img{width:45%;float:right;margin-left:90px}.sc_item__right h3{margin-top:0}.sc_item__right h3:hover{color:#00a651}.sc_item__right p{text-align:justify}
